import React, { useEffect, useState } from 'react'
import { Link, useLocation, useParams } from 'react-router-dom'
import { Pagination } from 'antd'
import 'antd/dist/reset.css'
import { recentBlogsApi, blogsApi, blogDetailsApi } from '../../apis/services'
import { errorResponseHandler } from '../../apis/http'
import Spinner from '../Spinner';
import CryptoJS from "crypto-js";
import imageNotFound from "./image.png"
const BlogDetails = () => {
    const [recentBlogs, setRecentBlogs] = useState([])
    const [currentPage, setCurrentPage] = useState(1)
    const [blogsPerPage, setBlogsPerPage] = useState(3)
    const [loading, setLoading] = useState(true);
    const [previousBlog, setPreviousBlog] = useState(null);
    const [data, setData] = useState({})
    const [notFound, setNotFound] = useState(false)

    const { id } = useParams();


    const encryptId = (id) => {
        return encodeURIComponent(
            CryptoJS.AES.encrypt(id.toString(), "mySecretKey").toString()
        );
    };


    const decryptedId = CryptoJS.AES.decrypt(
        decodeURIComponent(id),
        "mySecretKey"
    ).toString(CryptoJS.enc.Utf8);


    const location = useLocation()
    // const { data } = location.state || {}


    const fetchBlogDetails = async () => {

        console.log("decrypt", decryptedId)
        if (!decryptedId) {
            setNotFound(true)
            return;
        }

        try {
            const response = await blogDetailsApi(decryptedId);
            console.log("blog details", response)
            setData(response.blog);

        } catch (error) {
            errorResponseHandler(error);
        }
    }

    const fetchAllBlogs = async () => {
        try {
            const response = await blogsApi();

            let filterResponse = response.blogs.filter(
                (item) => item.category === data.category
            );

            filterResponse = filterResponse.filter((item) => item.id !== data.id);

            if (previousBlog) {
                filterResponse.unshift(previousBlog);
            }

            setRecentBlogs(filterResponse);
        } catch (error) {
            errorResponseHandler(error);
        } finally {
            setLoading(false);
        }
    };


    useEffect(() => {
        fetchBlogDetails();
    }, [id])

    useEffect(() => {
        fetchAllBlogs();
    }, []);
    useEffect(() => {
        const updateBlogsPerPage = () => {
            if (window.innerWidth < 768) {
                setBlogsPerPage(1)
            } else {
                setBlogsPerPage(3)
            }
        }

        updateBlogsPerPage()
        window.addEventListener('resize', updateBlogsPerPage)

        return () => {
            window.removeEventListener('resize', updateBlogsPerPage)
        }
    }, [])

    const paginatedBlogs = recentBlogs.slice(
        (currentPage - 1) * blogsPerPage,
        currentPage * blogsPerPage
    )

    const handleTop = () => {
        window.scroll(0, 0)
    }

    useEffect(() => {
        fetchAllBlogs();
    }, [data]);


    if (loading) {
        return <Spinner />
    }


    if (notFound) {
        return (
            <div
                style={{
                    height: "100vh",
                    display: "flex",
                    flexDirection: "column",
                    justifyContent: "center",
                    alignItems: "center",
                    textAlign: "center",
                    backgroundColor: "#f9fafb",
                    padding: "20px",
                }}
            >
                <h1
                    style={{
                        fontSize: "8rem",
                        fontWeight: "700",
                        color: "#313235ff",
                        marginBottom: "0",
                        lineHeight: "1",
                    }}
                >
                    404
                </h1>

                <h2
                    style={{
                        fontSize: "1.8rem",
                        fontWeight: "600",
                        color: "#111827",
                        marginTop: "10px",
                    }}
                >
                    There’s nothing here!
                </h2>

                <p
                    style={{
                        color: "#6b7280",
                        maxWidth: "400px",
                        marginTop: "8px",
                        fontSize: "1rem",
                    }}
                >
                    Sorry, the blog you’re looking for doesn’t exist or has been removed.
                </p>

                <Link
                    to="/blogs"
                    style={{
                        marginTop: "20px",
                        backgroundColor: "#2563eb",
                        color: "white",
                        padding: "10px 20px",
                        borderRadius: "8px",
                        textDecoration: "none",
                        fontWeight: "500",
                        transition: "background-color 0.2s ease",
                    }}
                    onMouseEnter={(e) => (e.target.style.backgroundColor = "#0e2986")}
                    onMouseLeave={(e) => (e.target.style.backgroundColor = "#2563eb")}
                >
                    Go Back To Blogs Page
                </Link>
            </div>

        );
    }


    return (
        <>
            <div className="page-header bg-section dark-section">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="page-header-box">
                                <h1 className="text-anime-style-3">Blogs</h1>
                                <nav className="wow fadeInUp">
                                    <ol className="breadcrumb">
                                        <li className="breadcrumb-item"><Link to="/">home</Link></li>
                                        <li className="breadcrumb-item"><i className="fa fa-chevron-right"></i></li>
                                        <li className="breadcrumb-item active">Blogs</li>
                                    </ol>
                                </nav>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="page-single-post">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="post-image">
                                <figure className="image-anime reveal" style={{ opacity: 1, visibility: "inherit" }}>
                                    <img src={data?.image} alt="" className='blog-img' />
                                </figure>
                            </div>
                            <div className='row'>
                                <div className='col-md-12'>
                                    <div className="post-item-body blog-detail">


                                        <div className="post-meta">

                                            <span><i className="fas fa-user"></i> {data.author}</span>
                                            <span><i className="far fa-calendar"></i>  {new Date(data.date).toLocaleString(
                                                "en-US",
                                                {
                                                    day: "2-digit",
                                                    month: "short",
                                                    year: "numeric",
                                                }
                                            )}</span>
                                            <span><i className="far fa-clock"></i> 7 min read</span>
                                        </div>
                                    </div>
                                </div>
                            </div>
                            <div className='post-content'>
                                <div className='row align-items-center'>

                                    <div className='col-md-12'>
                                        <h2 className="blog-title">{data?.title || "Blog Title"}</h2>
                                    </div>

                                </div>


                                <div class="">
                                    <div class="post-entry">

                                        <div dangerouslySetInnerHTML={{ __html: data?.description_1 }} />

                                        <blockquote class="wow fadeInUp mt-4" data-wow-delay="0.4s">
                                            <p>                                    {data?.quote}</p>
                                        </blockquote>

                                        <div class="wow fadeInUp" data-wow-delay="0.6s" dangerouslySetInnerHTML={{ __html: data?.description_2 }}></div>

                                    </div>

                                </div>
                            </div>

                        </div>
                    </div>
                </div>
            </div>

            {console.log("ashuss", paginatedBlogs)}

            {paginatedBlogs?.length > 0 && (

                <div className="page-blog">
                    <div className="container">
                        {paginatedBlogs?.length !== 0 && <div className="section-title text-center mxw-565 mx-auto">
                            <h2 className="mb-5">Related Posts</h2>
                        </div>}

                        <div className="row">
                            {paginatedBlogs?.map((blog, idx) => {
                                const encryptedId = encryptId(blog.id);
                                return (
                                    <div className="col-lg-4 col-md-6 col-sm-12 mb-4" key={idx}>
                                        <div className="post-item wow fadeInUp">
                                            <div className="post-featured-image">
                                                <Link to={`/blog-detail/${encryptedId}`} onClick={handleTop}>
                                                    <figure className="image-anime">
                                                        <img src={blog.image} alt="" />
                                                    </figure>
                                                </Link>
                                            </div>
                                            <div className="post-item-body ">
                                                <div className="post-meta">
                                                    <span><i className="fas fa-user"></i> {blog?.author}</span>
                                                </div>
                                                <div className="post-meta">
                                                    <span><i className="far fa-calendar"></i> {" "}
                                                        {new Date(blog.created_at).toLocaleDateString("en-US", {
                                                            day: "2-digit",
                                                            month: "short",
                                                            year: "numeric",
                                                        })}
                                                    </span>
                                                    <span><i className="far fa-clock"></i> 7 min read</span>
                                                </div>
                                                <div className="post-item-content">
                                                    <h3><Link to={`/blog-detail/${encryptedId}`} style={{ fontWeight: "bold" }} state={{ data: blog }} onClick={handleTop}>{blog.title.length > 40 ? blog.title.slice(0, 40) + "..." : blog.title}</Link></h3>
                                                    <p className="post-excerpt"
                                                        dangerouslySetInnerHTML={{
                                                            __html: blog.description_1.slice(0, 100) + "..."
                                                        }}
                                                    />
                                                </div>
                                                <div className="post-item-btn">
                                                    <Link to={`/blog-detail/${encryptedId}`} onClick={handleTop} className="readmore-btn">
                                                        read more
                                                    </Link>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                )
                            })}

                            {paginatedBlogs.length !== 0 && <div className="col-lg-12 d-flex justify-content-center mt-4">
                                <Pagination
                                    current={currentPage}
                                    pageSize={blogsPerPage}
                                    total={recentBlogs.length}
                                    onChange={(page) => setCurrentPage(page)}
                                    responsive
                                />
                            </div>}

                        </div>
                    </div>
                </div>
            )}

        </>
    )
}

export default BlogDetails
